.table {
  width: 100%;
}

.table {
  tr {
    transition: background .5s;
    background: white;
    border-bottom: 2px solid #f5f5f5;
    &:hover {
      background: $main-blue;
      td {
        color: white !important;
      }
    }
  }

  th, td {
    &:first-child {
      border-right: 1px solid #F5F5F5;
    }
    transition: color .5s;

    padding: 1em 1em;
  }
}



