@import '_reset.scss';

/**
Variables
**/
$white: #FFF;
$main-blue: #4285f4;
$very-light-blue: #e3edfe;
$body-dark: #212121;

@import 'header';
@import 'table';
* {
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
  color: $body-dark;
}

@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

button.clean {
  background: none;
  border: none;
  padding: 0;
  line-height: 0;
  cursor: pointer;
  outline: none;
  svg {
    cursor: pointer;
  }
}



